
import React from 'react';
import { DraggableEmployee } from './DraggableEmployee';
import type { Employee } from '@/types/schedule';
import { Users } from 'lucide-react';
import { SidebarGroup, SidebarGroupLabel, SidebarGroupContent } from '@/components/ui/sidebar'; // Import Sidebar components
import { ScrollArea } from '@/components/ui/scroll-area'; // Import ScrollArea

interface EmployeeListProps {
  employees: Employee[];
}

export const EmployeeList: React.FC<EmployeeListProps> = ({ employees }) => {
  return (
    <SidebarGroup>
        <SidebarGroupLabel className="flex items-center gap-2">
            <Users className="h-4 w-4" />
            Disponibles ({employees.length})
        </SidebarGroupLabel>
        <SidebarGroupContent>
            <ScrollArea className="h-[calc(100vh-150px)]"> {/* Adjust height as needed */}
                <div className="space-y-2 p-2">
                    {employees.length > 0 ? (
                    employees.map((employee) => (
                        <DraggableEmployee key={employee.id} employee={employee} />
                    ))
                    ) : (
                    <p className="text-sm text-muted-foreground text-center pt-4 italic">
                        No hay colaboradores para esta sede.
                    </p>
                    )}
                </div>
            </ScrollArea>
        </SidebarGroupContent>
    </SidebarGroup>
  );
};
